<template>
<div>
    <section class="content-header">
      <h1>Users</h1>
    </section>
    <section class="content">
      <div class="card box-warning">
        <div class="box-header with-border">
          <h3 class="box-title card-text">User Search</h3>
        </div>
        <div class="box-body">
          <div class="row">
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Name</label>
                <input type="text" class="form-control" placeholder="Enter Name ...">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Phone No.</label>
                <input type="tel" class="form-control" placeholder="Enter Phone No. ...">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Email</label>
                <input type="email" class="form-control" placeholder="Enter Email ...">
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Organisation</label>
                <select class="form-control">
                  <option>option 1</option>
                  <option>option 2</option>
                  <option>option 3</option>
                  <option>option 4</option>
                  <option>option 5</option>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label>Role</label>
                <select class="form-control">
                  <option>option 1</option>
                  <option>option 2</option>
                  <option>option 3</option>
                  <option>option 4</option>
                  <option>option 5</option>
                </select>
              </div>
            </div>
            <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
              <div class="form-group">
                <label style="display: block;"> &nbsp;&nbsp; </label>
                <button type="button" class="btn link1">Filter</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card box-black">
        <div class="box-header with-border">
          <h3 class="box-title card-text">Users Table</h3>
        </div>
        <div class="bottom-box-header">
          <div class="pull-left">
            <div class="Showingbox"> <span class="pull-left">Showing </span>
              <div class="pull-left drop">
                <select class="form-control">
                  <option value="25">25</option>
                  <option value="50">50</option>
                  <option value="100">100</option>
                </select>
              </div>
              <span class="pull-left">entries </span>
              <div class="clearfix"></div>
            </div>
          </div>
          <div class="pull-right"> <router-link to="/user/add" class="btn link1"> <span><i class="fa fa-plus-square-o" aria-hidden="true"></i> </span> Add User </router-link></div>
          <div class="clearfix"></div>
        </div>
        <div class="box-body">
          <div class="tablesection">
            <div class="table-responsive">
              <table class="table table-striped table-hover">
                <thead>
                  <tr>
                    <th style="text-align: center;" width="70">Sl No.</th>
                    <th>Name </th>
                    <th>Phone No.</th>
                    <th>Email</th>
                    <th>Organisation.</th>
                    <th>Role</th>
                    <th>Status </th>
                  </tr>
                </thead>

                 
                <tbody v-if="ulist && ulist.data"> 
                  <tr v-for="ua in ulist.data" :key="ua.id" > 
                    <td align="center">01</td>
                    <td><router-link :to="'/user/edit/'+ua.id" class="text-green">{{ua.name}}</router-link></td>
                    <td>{{ua.phone}}</td>
                    <td>{{ua.email}}</td>
                    <td>{{ua.organization}}</td>
                    <td>{{ua.type}}</td>
                    <td><span class="badge  bg-green">{{ua.status}}</span></td>
                  </tr>
                  
                </tbody>
              </table>
            </div>
          </div>
          <div class="paginationsectionbox">
            <div class="row" v-if="ulist && ulist.data">
              <div class="col-xs-12 col-sm-12 col-md-5 col-lg-5">
                <div class="Showingboxto1">Showing 1 to 18 of {{ ulist.last_page }} results</div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-7 col-lg-7">
                <div class="Showingboxto2">
                  <ul>                    
                   <li class="paginate_button" v-for="(links, index) in ulist.links" :key="index" :class="{'active disabled':links.active, 'disabled':!links.url}">
                        <a href="javascript:void(0);" v-html="links.label" ></a>
                    </li>                    
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>

 
  </div>
</template>

<script>
    export default {
      name:"userlist",
        data () {
            return {
                editmode: false,
                ulist:"",
                users : {},
                form: new Form({
                    id : '',
                    type : '',
                    name: '',
                    email: '',
                    password: '',
                    email_verified_at: '',
                })
            }
        },
        methods: {

            getResults(page = 1) {

                  this.$Progress.start();
                  
                  axios.get('api/user?page=' + page).then(({ data }) => (this.users = data.data));

                  this.$Progress.finish();
            },
 
            deleteUser(id){
                Swal.fire({
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    showCancelButton: true,
                    confirmButtonColor: '#d33',
                    cancelButtonColor: '#3085d6',
                    confirmButtonText: 'Yes, delete it!'
                    }).then((result) => {

                        // Send request to the server
                         if (result.value) {
                                this.form.delete('api/user/'+id).then(()=>{
                                        Swal.fire(
                                        'Deleted!',
                                        'Your file has been deleted.',
                                        'success'
                                        );
                                    // Fire.$emit('AfterCreate');
                                    this.loadUsers();
                                }).catch((data)=> {
                                  Swal.fire("Failed!", data.message, "warning");
                              });
                         }
                    })
            },
          loadUsers(){
            this.$Progress.start(); 
           // if(this.$gate.isAdmin()){
             // axios.get("api/user").then(({ data }) => (this.users = data));  

            axios.get("api/user")
                  .then((res) => {
                     
                      this.ulist = res.data.data 
                       console.log(this.ulist )                       
                  })
                  .catch(err => {
                      console.log(err)
                  });
                  //            }
            console.log(this.ulist)

            this.$Progress.finish();
          },
          
          createUser(){

              this.form.post('api/user')
              .then((response)=>{
                  $('#addNew').modal('hide');

                  Toast.fire({
                        icon: 'success',
                        title: response.data.message
                  });

                  this.$Progress.finish();
                  this.loadUsers();

              })
              .catch(()=>{

                  Toast.fire({
                      icon: 'error',
                      title: 'Some error occured! Please try again'
                  });
              })
          }

        },
        mounted() {
            console.log('User Component mounted.')
        },
        created() {

            this.$Progress.start();
            this.loadUsers();
            this.$Progress.finish();
        }
    }
</script>
